import { Injectable } from '@angular/core';
import {SocketService} from './socketio/socket.service'
import {Subject} from 'rxjs/Subject'
@Injectable()
export class DataService {

  public sensorData:any = {}
  public settingData:any = {}
  public co2subject = new Subject<any>();
  public settingSubject = new Subject<any>();
  public waterSubject = new Subject<any>();
  public datetimeSubject = new Subject<any>();

  public isUpdateSuccess:boolean = false;
  constructor(private io:SocketService) { 
    this.io.socket.on('SENSOR_DATA', (data:any)=>{
      // console.log(data);
      this.sensorData = data;
      this.datetimeSubject.next({
        date: data.date,
        time: data.time
      })
    });

    this.io.socket.on('CO2_STATUS', (data:any)=>{
      this.co2subject.next(data);
    });
     
    this.io.socket.on('REP_SETTING', (data:any)=>{
      this.settingData = data;
      this.settingSubject.next(data);
      
    });

    this.io.socket.on('WATER_STATUS', (data:any)=>{
      this.waterSubject.next(data);
    })
  }

}

