import { Injectable } from '@angular/core';
import * as io from "socket.io-client";

@Injectable()
export class SocketService {

  public hostname: string = window.location.protocol + "//" + window.location.hostname + ":" + 3001;
  public socket: SocketIOClient.Socket;
  
  constructor() { 
    console.log("[Socket.IO] Service initialize..")
    this.socket = io.connect(this.hostname);
    this.socket.on("connect", () => this.connect())
    this.socket.on("disconnect", () => this.disconnect());
    this.socket.on("reconect", () => this.reconnect());
  }

  connect():void {
    console.log("[SocketIO] Connected")
  }

  disconnect():void {
    console.log("[SocketIO] Disconnected")
  }

  reconnect(): void{
    console.log("[SocketIO] Reconnected")
  }

}



