import { Component, OnInit, OnDestroy } from '@angular/core';
import { DataService } from '../../data.service'
import { Subscription } from 'rxjs/Subscription'


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  public date: string = "00:00:00";
  public time: string = "2017-01-01";
  public dtsub: Subscription;
  constructor(private dataService:DataService) { }

  ngOnInit() {
    this.dtsub = this.dataService.datetimeSubject.asObservable().subscribe(data => {
      this.date = data.date;
      this.time = data.time.substring(0, data.time.length - 3);
      this.dtsub.unsubscribe();
    });
  }

  ngOnDestroy() {
this.dtsub.unsubscribe();
  }

}



